// react
import React from "react";

// third-party
import { Helmet } from "react-helmet";
import { Link } from "react-router-dom";

// data stubs
import theme from "../../data/theme";

function SitePageNotFound() {
  return (
    <div className="block">
      <Helmet>
        <title>{`Li 404`}</title>
      </Helmet>

      <div className="container">
        <div className="not-found">
          <div className="not-found__404">Oops! Li 404</div>

          <div className="not-found__content">
            <h1 className="not-found__title">Khng tm thy trang</h1>

            <p className="not-found__text">
              Chng ti khng th tm thy trang ny
              <br />
              Hy th tm kim
            </p>

            <form className="not-found__search">
              <input
                type="text"
                className="not-found__search-input form-control"
                placeholder="T kha..."
              />
              <button
                type="submit"
                className="not-found__search-button btn btn-primary"
              >
                Tm kim
              </button>
            </form>

            <p className="not-found__text">Hoc i n trang ch v th li.</p>

            <Link to="/" className="btn btn-secondary btn-sm">
              Tr v trang ch
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
}

export default SitePageNotFound;
